.feedback {
  background: linear-gradient(-45deg, #00f, #ff0) 0 0 / 400% 400%;
  animation: Gradient 15s infinite;
}

@keyframes Gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.feedback-form {
  max-width: 480px;
  background: none;
  border: 2px solid rgba(255, 255, 255, .5);
  border-radius: 20px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  color: #fff;
  border-bottom: 2px solid #fff;
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  color: #fff;
  background: none;
  border-color: #fff;
  border-radius: 40px;
  padding: 8px 12px;
  transition: all .2s ease-in-out;
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
}

input, textarea {
  font: inherit;
  color: #fff;
  background: none;
  border: none;
  outline: none;
  padding: 8px;
}

textarea {
  resize: none;
}

.feedback-form button:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}

